



































import Vue from "vue";
import Menu from "../components/Menu.vue";
import Modal from "../components/Modal.vue";
import inModal from '../components/InModal.vue';

import store from "../store/index";

export default Vue.extend({
  name: "Atendimento",
  components: {
    "menu-component": Menu,
    "modalLogin": Modal,
    "inModal": inModal
  },
  data() {
    return {
      isOpen: true,
      state: store.state,
      isModalVisible: false,
      inModal: false,
      isInModalVisible: false
    };
  },
  created(){
    if(!this.state.loginState){
      this.isModalVisible = true;
    }
  },

  methods: {
    handleChangePage() {
      this.$router.replace({ path: "/chat" });
    },
    closeModal() {



      setTimeout(() => {
        this.isModalVisible = false;


      }, 2000)

      setTimeout(() => {
        this.openInModal();
      }, 2000)



    },
    closeInModal(){
      this.isInModalVisible = false
    },
    openInModal(){
      this.state.loginState ? this.isInModalVisible = true : this.isModalVisible = true
    },

    async getAtendimento(){
      console.log('atendimento')
      await store.commit("atendimento");
    }
  },
});
